<template>
  <main>
    <div class="container">
      <label name="search">Search</label>
      <input
        type="text"
        placeholder="Search"
        name="searach"
        class="search"
        v-on:input="handleSearch($event)"
      />
      <button type="button" class="btn">
        <font-awesome-icon :icon="['fas', 'magnifying-glass']" beat-fade />
      </button>
    </div>
    <TableView :items="items" />
  </main>
</template>

// script

<script>
import "../Styles/NavBar.css";
import TableView from "../component/TableView.vue";
export default {
  name: "NavBar",
  components: { TableView },

  data() {
    return {
      items: [],
    };
  },

  created() {
    this.fetchData();
  },

  methods: {
    fetchData() {
      // Fetch data from the REST API
      const API_URL = "https://jsonplaceholder.typicode.com/posts";
      fetch(API_URL)
        .then((response) => response.json())
        .then((data) => {
          this.items = data;
        })
        .catch((error) => console.error("Error fetching data: ", error));
    },
    handleSearch(e) {
      let inpValue = e.target.value.toLowerCase().trim();
      let datas = this.items.filter((item) => {
        return item.title.toLowerCase().includes(inpValue);
      });

      this.items = datas;
    },
  },
};
</script>
